export default {
	name: 'uni-icon',
	props: {
		type: {
			type: String,
			default: ''
		},
		color: {
			type: String,
			default: '#333333'
		},
		size: {
			type: [Number, String],
			default: 16
		}
	},
	data() {
		return {

		};
	},
	computed: {
		uSize() {
			let size = this.size;
			return uni.upx2px(size);
		}
	},
	methods: {
		_onClick() {
			this.$emit('click');
		}
	}
}
